.App{
  margin: auto;
}
.nav{
  background: #F55A5A;
  height: 55px;
  display: flex;
  justify-content: center;
}
.nav-fill{
  height: 24px;
  width: 24px;
  margin-top: 12px;
}
.nav-text{
  font-weight: 500;
  font-size: 15px;
  color: #FFFFFF;
  margin-left: 5px;
}
.card{
  display: flex;
  padding: 20px;
  width: 470px;
}
.cards-list{
  margin: auto;
}
.location{
  text-transform: uppercase;
  color: #2B283A;
  font-weight: 400;
  font-size: 11px;
  line-height: 11px;
  letter-spacing: 0.17em;
  margin-left: 6px;
}
.title{
  font-size: 25px;
  font-weight: 700;
}
.description{
  line-height: 150%;
  font-size: 11px;
  display: flex;
  width: 326px;
}
.location-ph{
  border-radius: 5px;
  width: 250px;
  height: 200px;
}
span{
  font-weight: 700;
  color: #2B283A;
  font-size: 11px;
}
.details{
  margin-left: 20px;
}
.link{
  color: #918E9B;
  font-weight: 400;
  font-size: 11px;
  margin-left: 10px;
  margin-top: 8px;
}
.pin{
  height: 10px;
  width: 7px;
  margin-top: 11px;
}
.maps{
  display: flex;
}